import Application from './core/Application';
import UtilsService from './services/utils/index';
import WordsService from './services/words/index';
import GatewayService from './services/gateway/index.ts';
import './app/boot.ts';

const config = {
    services: [
        {
            id: 'utils'
            ,
            source: UtilsService
        }
        ,
        {
            id: 'words'
            ,
            source: WordsService
            ,
            baseUrl: 'https://script.google.com/macros/s/AKfycbxvQqYa_KgG0fQqxA6utQ1XSmBgoHKZhJsi7gyx5fgwJ3NKqupi/exec'
        }
        ,
        {
            id: 'gateway'
            ,
            source: GatewayService
        }
    ]
};

const App = new Application(config);

App.init();


