import {Component} from 'angular2/core';
import {WordComponent} from './word.component';
import {BaseComponent} from './base.component';
import {TitleComponent} from './title.component';
import {DropdownComponent} from './dropdown.component';

interface Word {
    translation: String
}

@Component({
    selector: 'my-app',
    template: `
            <div *ngIf="word">
                <ui-dropdown [data]="languages" (onValueChanged)="loadWords($event)"></ui-dropdown>
                <ui-title *ngIf="words" text="{{index + 1}}/{{words.length}}"></ui-title>
                <c-word [data]="word" (next)="next()"></c-word>
            </div>
            <div *ngIf="!word">There are no words left. Reload the page.</div>
     `,
    directives: [WordComponent, TitleComponent, DropdownComponent]
})

export class AppComponent extends BaseComponent {

    private words: Word[];

    public word: Word;

    private index: number = 0;

    private languages = [
        {
            id: 'english',
            name: 'English'
        }
        ,
        {
            id: 'russian',
            name: 'Russian'
        }
    ];

    constructor() {

        super();

        this.loadWords();

    }

    loadWords(mode) {

        console.log('loadWords', mode);

        this.emit('words://list', {mode}, (err, result) => {

            this.words = Array.shuffle(result);

            this.next();

        });

    }

    next() {

        //TODO make a loop here!
        this.index = this.words.indexOf(this.word) + 1;

        this.word = this.words[this.index];

    }

}



