import {Component, View} from 'angular2/core';
import Eventory from '../core/Eventory';

export class BaseComponent {

    private EventHub: any = new Eventory();

    emit(...args) {

        console.log('BaseComponent', args);

        this.EventHub.emit(...args);

    }

}


