import Eventory from './Eventory.ts';

interface ServiceConfig {

    id: string
    ,
    source: any

}

interface AppConfig {

    services: ServiceConfig[]

}

export default class Application {

    private config: AppConfig;
    private services: any[] = [];
    private EventHub: any = new Eventory();

    constructor(config: AppConfig) {

        this.config = config;

    }

    init() {

        this.config.services.map(c => this.services.push(new c.source(this)));

        this._initServices(this.services);

    }

    _initServices(services) {

        services.map(c => c.init());

    }

    subscribeListener(key, cb) {

        console.log('subscribeListener', key, cb);

        this.EventHub.on(key, cb);

    }

    emit(key, payload, cb) {

        this.EventHub.emit(key, payload, cb);

    }

}




