import superagent = require('superagent');

export default function(url, cb) {

    let req = superagent['get'](url);

    req.set('Accept', 'application/json');

    req.end((err, result) => adaptResponse(err, result, cb));

}

const adaptResponse = (err, {body}, cb) => {

    err = err || body.error;

    err ? cb(err) : cb(null, body.result);

};


