const hub: any = new Map();

export default class Eventory {

    on(key: string, cb: any): void {

        hub.set(key, cb);

    }

    emit(key: string, payload: any, cb: any): void {

        if (!cb) {

            cb = payload;

            payload = null;

        }

        const path = key.split('://');

        const handler = hub.get(`on${String.capitalize(path[0])}${String.capitalize(path[1])}`);

        if (!handler) throw new Error(`No handler for: ${key}`);

        handler(payload, cb);

    }

}


