import remote from './imp/remote';

export default class Service {

    private id: string;
    private app: any;

    private _subscribeListeners: any = () => {

        let methods: string[] = Object.keys(this.constructor.prototype);

        methods.map((m: string) => {

            if (~m.indexOf('on')) this.app.subscribeListener(m, this[m].bind(this));

        });

    };

    constructor(app) {

        this.app = app;

        this.id = this.constructor.name;

    }

    init() {

        this._subscribeListeners();

        this.log('inited');

    }

    log(...args) {

        console.log(`${this.id}:`, ...args);

    }

    remote(url, cb) {

        remote(url, cb);

    }

    emit(key, payload, cb) {

        this.app.emit(key, payload, cb);

        this.log(`${key}:emitted`);

    }

}


