import {Component, View, Input, Output, EventEmitter} from 'angular2/core';
import {TitleComponent} from './title.component';
import {ButtonComponent} from './button.component';

@Component({selector: 'c-word'})

@View({
    template: `
        <ui-title [text]="data.word"></ui-title>
        <ui-title *ngIf="showTranslation" [text]="data.translation"></ui-title>
        <ui-button (click)="onKnowButton($event)" caption="Know"></ui-button>
        <ui-button (click)="onForgottenButton($event)" caption="Forgotten"></ui-button>
    `,
    directives: [TitleComponent, ButtonComponent]
})

export class WordComponent {

    @Input() data;
    @Output() next = new EventEmitter();

    public showTranslation: boolean = false;

    onKnowButton(...args) {

        this.showTranslation = false;

        this.next._next();

    }

    onForgottenButton() {

        this.showTranslation = true;

    }

}


